import React from 'react';
import {  View, ScreenSpinner, Panel, Epic, Button, PanelHeader, Panar, Avatar, Div, HeaderButton, Cell, Snackbar, Gallery, Group} from '@vkontakte/vkui';

// Компонент профиля
class Profile extends React.Component {
	construcor(props) {
       //super(props);

       this.state = {
          fetchedUser:this.props.fetchedUser
     }
  }

	render() {
		return(

            <View id="profile-view" activePanel="profile-panel">
                <Panel id="profile-panel">
                  <PanelHeader>Профиль</PanelHeader>
                  
                     <Avatar src="this.state.fetchedUser.photo" />
                </Panel>
            </View>
		);           
	}
}

export default Profile;